/* Measurements */
.kadence-title-bar {
  margin-bottom: 8px;
  display: flex;
  align-items: center; }
  .kadence-title-bar .kadence-control-title {
    flex-grow: 1; }
  .kadence-title-bar button.components-button.has-icon.is-reset {
    width: 20px;
    min-width: 20px;
    padding: 0 3px;
    margin: 0 5px 0 0; }

.kadence-range-control-range .components-range-control__root svg {
  margin-top: 2px; }

.kadence-controls-content .measure-input-wrap .input-setting-toggle {
  height: 14px; }
  .kadence-controls-content .measure-input-wrap .input-setting-toggle .components-button.input-setting-toggle-btn {
    width: 100%;
    height: 14px;
    padding: 0;
    line-height: 12px;
    font-size: 7px !important;
    margin: 0;
    display: block;
    border: 1px solid #e2e4e7;
    border-radius: 0;
    text-transform: uppercase;
    background-color: #fff; }

.kb-measure-control .kadence-controls-content {
  align-items: flex-end; }

.components-popover.kadence-range-popover-settings .components-popover__content > div {
  padding: 30px 20px;
  min-width: 280px; }

.components-button-group.kb-preset-size-options {
  display: flex;
  flex-flow: wrap;
  margin: -1%; }

.components-button-group.kb-preset-size-options button.components-button.kb-preset-sizes {
  flex-basis: 18%;
  width: auto;
  min-width: auto;
  margin: 1%;
  border-radius: 0;
  text-align: center;
  padding: 0 2px;
  justify-content: center; }

.components-button-group.kb-measure-responsive-options .components-button.kb-responsive-btn {
  background: transparent;
  height: auto;
  border: 0;
  box-shadow: none;
  padding: 2px 4px;
  margin-bottom: 0;
  outline: 0;
  color: #A0AEC0;
  width: auto;
  font-size: 10px; }
  .components-button-group.kb-measure-responsive-options .components-button.kb-responsive-btn:hover {
    color: #718096; }
  .components-button-group.kb-measure-responsive-options .components-button.kb-responsive-btn.is-active {
    color: #007cba;
    background: transparent; }
  .components-button-group.kb-measure-responsive-options .components-button.kb-responsive-btn .dashicon {
    width: 15px;
    height: 15px;
    font-size: 15px; }

.kadence-controls-content {
  display: flex; }
  .kadence-controls-content .kadence-range-control {
    flex-grow: 1;
    margin-right: 2px;
    margin-bottom: 0; }
    .kadence-controls-content .kadence-range-control .components-base-control__field {
      margin-bottom: 0;
      display: flex; }
  .kadence-controls-content .measure-input-wrap {
    margin-right: 2px;
    flex-grow: 1; }
    .kadence-controls-content .measure-input-wrap .components-base-control.measure-inputs {
      margin: 0;
      position: relative; }
      .kadence-controls-content .measure-input-wrap .components-base-control.measure-inputs .components-base-control__field {
        margin-bottom: 0; }
      .kadence-controls-content .measure-input-wrap .components-base-control.measure-inputs input[type=number] {
        padding: 0 5px;
        margin: 0;
        -moz-appearance: textfield; }
        .kadence-controls-content .measure-input-wrap .components-base-control.measure-inputs input[type=number]::-webkit-outer-spin-button, .kadence-controls-content .measure-input-wrap .components-base-control.measure-inputs input[type=number]::-webkit-inner-spin-button {
          -webkit-appearance: none;
          margin: 0; }
        .kadence-controls-content .measure-input-wrap .components-base-control.measure-inputs input[type=number][disabled] {
          background: #e4e7ea; }
  .kadence-controls-content .measure-label {
    display: none;
    font-size: 11px;
    line-height: 16px;
    padding: 3px 0;
    text-align: center; }
    .kadence-controls-content .measure-label svg {
      height: 16px;
      width: 16px; }

.kb-measure-control .measure-input-top .measure-inputs .components-base-control__field:before {
  content: '';
  display: block;
  border-top: 3px solid #757575;
  width: 100%;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
  position: absolute;
  left: 0;
  top: 0; }

.kb-measure-control.kb-measure-corners-control .measure-input-top .measure-inputs .components-base-control__field:before {
  width: 15px;
  height: 15px;
  border-left: 3px solid  #757575;
  border-top: 3px solid  #757575;
  border-top-right-radius: 0px; }

.kb-measure-control .measure-input-right .measure-inputs .components-base-control__field:before {
  content: '';
  display: block;
  border-right: 3px solid #757575;
  width: 0;
  height: 100%;
  border-bottom-right-radius: 2px;
  border-top-right-radius: 2px;
  position: absolute;
  right: 0;
  top: 0; }

.kb-measure-control.kb-measure-corners-control .measure-input-right .measure-inputs .components-base-control__field:before {
  width: 15px;
  height: 15px;
  border-right: 3px solid  #757575;
  border-top: 3px solid  #757575;
  border-bottom-right-radius: 0px; }

.kb-measure-control .measure-input-bottom .measure-inputs .components-base-control__field:before {
  content: '';
  display: block;
  border-bottom: 3px solid #757575;
  width: 100%;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
  position: absolute;
  right: 0;
  bottom: 0; }

.kb-measure-control.kb-measure-corners-control .measure-input-bottom .measure-inputs .components-base-control__field:before {
  width: 15px;
  height: 15px;
  border-right: 3px solid  #757575;
  border-bottom: 3px solid  #757575;
  border-bottom-left-radius: 0px; }

.kb-measure-control .measure-input-left .measure-inputs .components-base-control__field:before {
  content: '';
  display: block;
  border-left: 3px solid #757575;
  height: 100%;
  width: 0;
  border-bottom-left-radius: 2px;
  border-top-left-radius: 2px;
  position: absolute;
  left: 0;
  bottom: 0; }

.kb-measure-control.kb-measure-corners-control .measure-input-left .measure-inputs .components-base-control__field:before {
  width: 15px;
  height: 15px;
  border-left: 3px solid  #757575;
  border-bottom: 3px solid  #757575;
  border-top-left-radius: 0px; }

.kadence-units {
  max-width: 60px;
  display: flex; }
  .kadence-units .components-button {
    padding-top: 2px;
    padding-bottom: 2px; }
    .kadence-units .components-button svg {
      width: 16px; }

.kadence-units button.components-button.is-active.is-single.is-small {
  min-width: 20px;
  height: 30px;
  border: 1px solid #e2e4e7;
  background-color: #fff;
